.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  background-color: #222;
  color: white;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}

.App-titleWrapper {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-left: 10px;
  padding-right: 10px;
  border: 5px solid;
  -webkit-border-image: -webkit-gradient(linear, right top, left top, from(#f7ff00), to(#db36a4)) 10;
  -webkit-border-image: -webkit-linear-gradient(right, #f7ff00, #db36a4) 10;
       -o-border-image: -o-linear-gradient(right, #f7ff00, #db36a4) 10;
          border-image: -webkit-gradient(linear, right top, left top, from(#f7ff00), to(#db36a4)) 10;
          border-image: linear-gradient(to left, #f7ff00, #db36a4) 10;
}

.App-title {
  margin: 10px;
  text-align: center;
  color: white;
  font-size: 2.0em;
  font-weight: bold;
}

.App-theMagic {
  margin-top: 20px;
  margin-bottom: 20px;
  text-align: center;
  font-size: 1.5em;
  font-weight: bold;
}

.App-rateWrapper {
  margin-bottom: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.App-rateThumb {
  margin: 10px;
  /* border: 3px solid white; */
  font-size: 1.5em;
  cursor: pointer;
}

.App-another {
  padding: 5px;
  border-radius: 100px;
  border: 3px solid #db36a4;
  background-color: #222;
  color: white;
  cursor: pointer;
  font-size: 1.25em;
  font-weight: bold;
  -webkit-transition: background-color 0.125s ease-in;
  -o-transition: background-color 0.125s ease-in;
  transition: background-color 0.125s ease-in;
}
.App-another:hover {
  background-color: #db36a4;
  border: 3px solid #db36a4;
  -webkit-transition: background-color 0.125s ease-in;
  -o-transition: background-color 0.125s ease-in;
  transition: background-color 0.125s ease-in;
}

